* {
  margin: 0;
}
body{
  background-color: #f5f6fa;
}
.app{
  display: flex;
  justify-content: space-evenly;
  padding:20px;
 
}
.app__dropdown{
  background-color: white;
}

.app__left{
  flex:0.9;
}
.app__right{
  display: flex;
  flex-direction: column;
}
.app__right .MuiCardContent-root{
  display:flex;
  flex-direction: column;
  flex-grow: 1;
}
.app__right .app__graph{

  flex-grow: 1;
}


.app_header >h1 {
color:#fc3c3c;
font-size: 2rem;
}
.app_header { 
  display: flex;
  margin-bottom: 20px;
  justify-content: space-between;
}
.app__stats{
  display: flex;
    justify-content: space-between;
 
}
.app__graphtitle{
  margin-top: 20px;
  margin-bottom: 20px;
}
.country__flag{
  height:20px;
  width:20px;
  margin-right: 10px;
}

@media(max-width:990px){
  .app{
    flex-direction: column;
  }
  
}